import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import HttpsRedirect from 'react-https-redirect';
import { Navigate } from 'react-router-dom';

import {
  BrowserRouter,
  Routes,
  Route,
} from "react-router-dom";
import App from "./App";
import WelcomePage from "./WelcomeComponent/WelcomePage";
import {MainPage} from './MainComponent/MainComponent';

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <HttpsRedirect>
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Navigate replace to="/coding-helper"/>} />
        <Route path="/coding-helper" element={<App />}>
          <Route path="assistant" element={<MainPage />} />
          <Route path="" element={<WelcomePage />} />
        </Route>
      </Routes>
    </BrowserRouter>
  </HttpsRedirect>);
